.wrapperFormName {
  position: relative;
  margin-bottom: 20px;
}

.wrapperFormEmail {
  position: relative;
  margin-bottom: 35px;
}

.inputLabel {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  pointer-events: none;
  transition: all 0.3s;
  z-index: 1;
  color: #fff;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.fieledForm {
  width: 460px;
  height: 50px;
  padding: 0 0 13px 0;
  border: 0;
  color: #fff;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  border-bottom: 1px solid #777;
  background-color: transparent;

  @media (max-width: 1023px) {
    width: 296px;
    font-size: 16px;
    height: 35px;
    padding-bottom: 5px;
  }
}

.hidden {
  opacity: 0;
  transform: translateY(-100%);
}

.inputSkrin {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-decoration-line: underline;
  width: 460px;
  height: 120px;

  @media (max-width: 1023px) {
    width: 296px;
  }
}

input[type="file"] {
  display: none;
}

.inputLabelType {
  text-align: center;
  width: 460px;
  height: 120px;
  margin-bottom: 35px;
  border: 1px dashed #777;
  color: #fff;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;

  @media (max-width: 1023px) {
    width: 296px;
  }
}

.spanType {
  font-size: 12px;
  @media (max-width: 1023px) {
    display: none;
  }
}

.inputLabelType span {
  text-decoration-line: none !important;
}

.buttonSubmit {
  padding: 15px 50px;
  align-items: center;
  gap: 10px;
  background: #f8f200;

  margin-bottom: 15px;

  color: #000;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.fieldForm {
  display: flex;
  align-items: center;
  color: #fff;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.checkboxWrapper {
  display: flex;
  align-items: center;
}

.fieldForm input[type="checkbox"] {
  margin-right: 10px;
}
